export default{
    /**
       * /data_count/data_day_count)
       * {
          region:'',         //ID
          firm_id:'',        //ID
          microprobe_type:'' //
          date:'',           // 20170905
          type:'' //all               
                  //customer_archives 
                  //virtual_archives  
                  //mobile_archives   
                  //mac_archives      MAC
                  //netlogs           
       * 
       */
      DayCount(data){
        this.baseBag.type='GET';
        this.baseBag.method='checkAndCount/dailyCount';
        data.query=JSON.stringify(data.query);
        this.baseBag.data=data || {};
        return this.post(this.baseBag);
      } 
}